export default {
	data() {
		return {
			datalist: [], // 剧情列表
			empty: {},
			push: 'push',
			change: 'change',
			workinfo: {},
			categoryId: '',
			startPlotId:"",
			unfold: true
		};
	},
	methods: {
		godramaInfo(data){
		    let dramaInfo = JSON.stringify(data);
			console.log("点击事件")
			uni.navigateTo({
				url:'../dramaInfo/dramaInfo?dramaInfo='+encodeURIComponent(dramaInfo)
			});
		},
		// 设置开头的剧情
		settingstart(data){
			let that=this;
			uni.showModal({
				title: '提示',
				content: '确定要设置此剧情为开始吗',
				success: function(res) {
					if (res.confirm) {
						that.$request
							.post('/project/' + that.workinfo.id + '/start_num?startNum='+data.plotNum+"&startPlotId="+data.plotId)
							.then(res => {
								that.getstartnum();
							})
							.catch(error => {
								console.error('error:', error);
							});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 展开收缩
		is_unfold() {
			this.unfold = !this.unfold;
		},
		// 获取剧情
		getdatalist() {
			let that = this;
			that.$request
				.get('/plot/text/?categoryId=' + this.categoryId)
				.then(res => {
					console.log(res, '请求的数据');
					that.datalist = res.data;
				})
				.catch(error => {
					console.error('error:', error);
				});
		},
		// 获取开始剧情
		getstartnum() {
			let that = this;
			that.$request
				.get('/project/' + this.workinfo.id + '/start_num')
				.then(res => {
					console.log(res, '请求的数据');
					that.startPlotId = res.data.startPlotId;
					
					console.log(that.startPlotId ,"开头的剧情")
				})
				.catch(error => {
					console.error('error:', error);
				});
		},
		deletedata(data) {
		 console.log(data);
			let that = this;
			uni.showModal({
				title: '提示',
				content: '确定删除此条数据吗',
				success: function(res) {
					if (res.confirm) {
						that.$request
							.delete('/plot/'+data.plotId+'/')
							.then(res => {
								that.getdatalist();
							})
							.catch(error => {
								console.error('error:', error);
							});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		operation(data, type) {
			console.log(data,"data");
		
			let transmit={
				categoryId:this.categoryId
			};
			let transmitdata=JSON.stringify(transmit);
			if (type === 'push') {
				uni.navigateTo({
					url:'./editordramaList?transmitdata='+encodeURIComponent(transmitdata)
				});
			} else if (type === 'change') {
				let DramaGrouping = JSON.stringify(data);
				uni.navigateTo({
					url: './editordramaList?DramaGrouping=' + encodeURIComponent(DramaGrouping)
				});
			}
		}
	},
	mounted: function() {},
	onShow() {
		this.getdatalist();
		this.getstartnum();
	},
	onLoad(data) {
		let workinfo = uni.getStorageSync('workinfo');
		this.workinfo = workinfo;
		console.log(data.categoryId, '剧情ID');
		this.categoryId = data.categoryId;
	}
};
